<template>
  <div id="app">
    <!---->
		<header class="header">
			<!--  -->
			<div class="swiper-container">
				<div class="swiper-wrapper">
					<div class="swiper-slide"><img src="/static/img/1-1FI01SF4D1.jpg"></div>
					<div class="swiper-slide"><img src="/static/img/1-1FI01SF4D1.jpg"></div>
					<div class="swiper-slide"><img src="/static/img/1-1FI01SF4D1.jpg"></div>
				</div>
				<!--  -->
				<div class="swiper-pagination"></div>
			</div>
		</header>
    <marquee direction="left" scrolldelay="100" width="100%" onmouseover="this.stop()" onmouseout="this.start()" scrollamount="10" style="height: 25px; line-height: 25px; color: rgb(58, 160, 246); white-space: nowrap; width: 100%;">
			<p style="color:#3AA0F6;">1500-19:00:1115009958</p>
	    </marquee>
	    <!---->
	    <div class="nav">
	    		<router-link to="#"></router-link>
	    		<router-link to="#"></router-link>
	    		<router-link to="#"></router-link>
	    </div>
    <router-view/>
  </div>
</template>

<script>
  import 'swiper/dist/css/swiper.css'
	import Swiper from 'swiper'
export default {
  name: 'App',
  mounted: function() {
			// 
			new Swiper('.swiper-container', {
				loop: true, //
				// 
				pagination: {
					el: '.swiper-pagination',
				},
				autoplay: {
					delay: 3000, //
					disableOnInteraction: false, //
				}
			})
		}
}

</script>

<style>
html,body,#app{
	width: 100%;
	height: 100%;
	overflow: hidden;
	overflow-y: auto;
}
.swiper-container img {
	width: 100%;
}
/*<!---->*/

.nav {
	display: flex;
	height: 34px;
	margin: 0 10px;
	border-radius: 5px;
	background-color: #00a0fd;
}

.nav a {
	display: inline-block;
	flex: 1;
	text-align: center;
	line-height: 34px;
	text-decoration: none;
	color: #FFFFFF;
	border-right: 1px solid #eee;
}

.nav a:last-child{
	border: none;
}
</style>



